.un-show {
  background-color: #eee;
  /*border: 3px solid #ddd;*/
  /*border-style: inset;*/
  padding: 1px 0 0;
	border-top: 3px solid #DDD;
	border-left: 3px solid #DDD;
	border-right: 3px solid #BBB;
	border-bottom: 3px solid #BBB;
}
.test-mas span {
  margin-left: 6px;
}
.glyphicon-flag {
  color: blue;
}
.glyphicon-asterisk {
  color: red;
}
.container {
  padding-top: 30px;
}
.bs-example {
  margin-right: 0;
  margin-left: 0;
  background-color: #fff;
  border-color: #ddd;
  border-width: 1px;
  border-radius: 4px 4px 0 0;
  box-shadow: none;

  position: relative;
  margin: 0 -15px 15px;
  border-style: solid;
  padding: 15px 15px;
}
.main-borad {
  border-width: 1px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  margin: -15px -15px 15px;
  padding: 14px 14px;
  background-color: #f7f7f9;
  border: 1px solid #e1e1e8;
  border-top-width: 0;
}



